 API производится посредствам токенов, за основу взята библиотека joser."),_c('br'),_vm._v(" Авторизация через социальные сети реализована при помощи google firebase на фронтенде, на бэкенд отправляются uid пользователя (он уникален для любого пользователя firebase и выглядит примерно так: UWQhELQTgfhTejn2FKFtoh1PzO73) в качестве логина и он же в хешированом виде в качестве пароля (сериалайзер модели пользователя не выдаёт информацию о логине и пароле клиентской стороне), а так же передаётся прочяя информация, необходимая для создания полноценного пользователя(ссылка на аватар, отоброжаемое имя и.т.п)."),_c('br'),_vm._v(" Фиксация онлайна пользователей реализована при помощи expire-записей в redis. Продолжительность сессии устанавливается в настройках django-проекта.")]),_c('p',[_vm._v("Аватары пользователей после загрузки сжимаются пропорционально до заданных размеров и хранятся на AWS S3 Cloud, как и прочие медиа файлы проекта. Допустимые параметры изображений, разрешенных к загрузке, а так же параметры сжатия устанавливаются в настройках django проекта. Аватары пользователей делятся на 3 группы: по умолчанию, аватар из сторонних источников и загруженный пользователем аватар. Приоритет их отображения следующий: В первую очередь отображается автар, загруженный пользователем, если он отсутствует, показывается аватар из стороннего источника (полученный, например, при OAuth авторизации), если же отсутствует и он, отображается аватар по умолчанию.")]),_c('p',[_vm._v("Структурно приложение представляет собой форумы, включающие в себя ветки, которые, в свою очередь, включают в себя темы, состоящие из сообщений."),_c('br'),_vm._v(" Реализована возможность создавать закрытые форумы и ветки, с функцией добавления в них участников создателем. В окне добавления участников создан реактивный поиск с фильтрацией списка по символам, с которых пользователь начинает ввод."),_c('br'),_vm._v(" Темы и сообщения выдаются пагинированно и добавляются в ленту при помощи бесконечного скролла."),_c('br'),_vm._v(" Валидация данных большей своей частью реализуется на серверной стороне. Настроены соответствующие пермишены для пользователей."),_c('br'),_vm._v(" Приложение я старался сделать максимально адаптивным. На маленьких экранах у основного компонента, отображающего список тем, появляется возможность показывать сообщения, а компонент, отвечавший за отображение сообщений, исчезает. Так же высота приложения адаптируется под высоту вьюпорта при скрытии адресной строки в мобильном браузере google chrome.")]),_c('p',[_vm._v("Для тем и сообщений проработана система рейтинга. Пользователь имеет возможность их оценить негативно или положительно. Эти действия оказывают влияние на общую репутацию автора сообщения, которая отоброжается под его аватаром. Изменения происходят в реактивном режиме с выполнением соответствующих AJAX-запросов в фоне."),_c('br'),_vm._v(" Так же каждая тема и сообщение имеет фиксацию пользователей, запрашивающих её, на этом основывается система отоброжения количества непрочитанных пользователем тем в ветке и сообщений в теме.")]),_c('p',[_vm._v("В формах отправки сообщений имеется поддержка эмоджи. Эмоджи хранятся в уникод символах и парсятся с последующей заменой на изображения при маунте компонента, содержащего их.")]),_c('p',[_vm._v("У меня было большое желание использовать celery, но не было очевидных идей для его применения, потому я решил приспособить его для отслежования завершений пользовательских сессий. Был написан мидлвэр, логирующий запросы пользователя на сервер, хранящий логи в redis. И по завершении пользовательской сессии лог запросов пользователя с кодами ответов сервера отправляется мне на e-mail, а логи удаляются из базы. Эта информация теоретически будет полезна в выявлении некорректных ответов сервера, а так же производить общий анализ действий пользователей в приложении. Для мониторинга деятельности celery был подключен flower, который можно обнаружить на 5555 порту.")]),_c('p',[_vm._v("Так же в бэкенд приложения интегрирован логгер Sentry.")]),_c('p',[_vm._v("Сервер - nginx, запущен на vps от яндекс облака. Деплой приложеня осуществлялся при помощи Docker. За исключением базы данных, она развернута в файловой системе сервера. Так же я подключил к нему, имеющийся у меня домен.")]),_c('p',[_vm._v("На этом я заканчиваю описание проделанной мною на данный момент работы, но не заканчиваю работу над этим приложением, есть еще много фич, которые я хотел бы реализовать и много багов, которые хочу пофиксить. Благодарю вас, за уделенное время и внимание к моему скромному проекту, надеюсь, оно потрачено не зря! :)")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"api-info__title",attrs:{"id":"auth"}},[_c('h1',[_vm._v("Авторизация")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"api-info__subtitle"},[_c('p',[_vm._v("API приложения использует токен-авторизацию, для его использования вашим запросам понадобится заголовок \"Authorization\" со значением формата \"Token XXXXXXXXXXXXXXXXXXX\", где вместо символов X будет находиться ваш токен авторизации.")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"api-info__subtitle",attrs:{"id":"auth-token-get"}},[_c('p',[_vm._v("Получить токен авторизации используя логин и пароль:")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"api-info__code"},[_vm._v(" Путь: "),_c('pre',[_vm._v("/api/v1/auth/token/login/\nAllowed methods: POST\nContent-Type: application/json\n")]),_vm._v(" Привер запроса: "),_c('pre',[_vm._v("request: /api/v1/auth/token/login/\nmethod: POST\n\n{\n    \"password\": \"password\",\n    \"username\": \"username\"\n}\n")]),_vm._v(" Пример ответа: "),_c('pre',[_vm._v("response: Http 200 OK\n\n{\n    \"auth_token\": \"##################################\"\n}\n")])])},function () {var _vm=t